.categoryItemShort {
    padding-top: 30px;
    .categoryItemShort_Title {
        padding: 30px 0;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 32px;
        font-weight: bold;
    }
    .categoryItemShort_content {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        gap: 15px;
        .item_short {
            padding: 15px;
            width: 100%;
            max-width: 170px;
            
            min-height: 250px;
            border-radius: 24px;
            border: 1px solid #e5e5e5;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            .img {
                width: 100%;
                height: 100%;
                max-width: 130px;
                height: auto;
            }
            .title {
                color: #615f78;
                font-size: 18px;
                line-height: 18px;
                font-weight: bold;
            }
            .price {
                color: #33ccff;
                font-size: 34px;
                line-height: 37px;
                font-weight: bold;
            }
            .subTitle {
                color: #615f78;
                font-size: 14px;
                line-height: 15px;
            }
            .weight {
                color: red;
                font-size: 13px;
                line-height: 24px;
            }
            .more {
                padding: 5px;
                color: #fff;
                background-color: #33ccff;
            }
            .more_active {
                height: 28px;
                color: #000063;
            }
        }
        .item_short_active{
            border: 1px solid #33ccff;
        }
    }
}
