.ourServices {
    .ourServices_content {
        .subTitle_text {
            width: 100%;
            max-width: 620px;
            margin: 0 auto;
            text-align: center;
        }
        .ourServices_cards {
            padding-top: 30px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .ourServices_item {
                padding: 20px;
                width: 100%;
                max-width: 350px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;
                border: 2px solid #989898;
                border-radius: 12px;
                .mainImg{
                    display: flex;
                    width: 100%;
                    max-width: 306px;
                    height: 100%;
                    max-height: 204px;
                }
                .mainSvg{
                    margin-top: 10px;
                    width: 40px;
                    height: 40px;
                }
                .item_title{
                    margin: 10px 0;
                    font-size: 16px;
                    font-weight: bold;
                }
            }
        }
    }
}
