.category {
    display: flex;
    flex-direction: column;
    .category_content {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        .category_item {
            padding: 20px;
            border: 2px solid #989898;
            border-radius: 12px;
            cursor: pointer;
        }
        .movers {
            display: flex;
            flex-direction: row;
            .movers_main {
                display: flex;
                flex-direction: column;
                justify-self: center;
                align-items: center;
                .movers_main_title {
                    max-width: 200px;
                    font-size: 32px;
                    font-weight: bold;
                    text-align: center;
                }
                .movers_main_price {
                    margin-top: 10px;
                    width: 100px;
                    height: 100px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    background-color: #f8f8fa;
                    color: #6f6e84;
                    border-radius: 100%;
                    .top_price {
                        font-size: 18px;
                    }
                    .center_pricу {
                        color: #4e8002;
                        font-size: 30px;
                    }
                    
                }
                .movers_main_subTitle{
                    padding-top: 10px;
                    display: flex;
                    flex-direction: column;
                    font-size: 11px;
                    .red{
                        color: red;
                    }
                }
            }
        }
        .cars{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            .cars_title{
                font-size: 20px;
                font-weight: bold;
            }
            .cars_subTitle{
                padding-top: 10px;
            }
        }
    }
}

.category_item_active {
    border-color: #33ccff !important;
}
