//      Main styles
* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*:before,
*:after {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}
body {
    font-size: 14px;
    font-family: "Montserrat", sans-serif;
    color: #323232;
    position: relative;
}

a{
    text-decoration: none;
    color: #fff;
}

//      Container

.container {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 10px;
}
