.movers_full_title {
    margin: 30px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    font-weight: bold;
}
.movers_full {
    padding: 15px !important;
    display: flex;
    flex-direction: column;
    border: 1px solid #33ccff;
    border-radius: 24px;
    .movers_full_top {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        .price {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .title {
                font-size: 40px;
                line-height: 40px;
                font-weight: bold;
            }
            .pricePerHour {
                color: #33ccff;
                font-size: 60px;
                line-height: 70px;
                font-weight: bold;
            }
            .subTitle {
                font-size: 24px;
                line-height: 24px;
            }
        }
    }
    .movers_full_center {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        .characterization {
            padding: 20px;
            width: 100%;
            max-width: 330px;
            display: flex;
            flex-direction: column;
            border: 2px solid #33ccff;
            border-radius: 24px;
            .title {
                margin-bottom: 10px;
                display: flex;
                color: #33ccff;
                font-size: 22px;
                line-height: 22px;
                font-weight: bold;
            }
            .characterization_item {
                padding-bottom: 3px;
                margin-bottom: 3px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                width: 100%;
                position: relative;
                &::before {
                    content: "";
                    display: flex;
                    width: 100%;
                    height: 1px;
                    background-color: #323232;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                }
            }
        }
        .price {
            display: flex;
            flex-direction: column;
            width: 100%;
            max-width: 300px;
            .price_item {
                padding: 20px;
                &:first-child {
                    border: 2px solid #505050;
                    border-radius: 6px;
                    .price_amount {
                        color: #33ccff;
                    }
                }
                &:nth-child(2) {
                    border-bottom: 1px solid #505050;
                }
                .price_amount {
                    font-size: 20px;
                    line-height: 25px;
                    font-weight: bold;
                    color: #6f6e84;
                }
            }
        }
    }
    .movers_full_bottom {
        padding-top: 20px;
        display: flex;
        flex-direction: column;
        .text {
            margin-bottom: 10px;
            font-size: 18px;
            line-height: 20px;
        }
        .red {
            color: red;
        }
    }
}
