.reviews{
    padding-bottom: 20px;
    background-color: #33CCFF;
    .reviews_items{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        .reviews_item{
            padding: 15px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            .reviews_item_name{
                font-size: 18px;
                font-weight: bold;
            }
            .reviews_item_svg{
                margin: 10px 0;
                width: 100%;
                max-width: 107px;
                height: auto;
            }
            .reviews_item_text{
                font-size: 15px;
                line-height: 1.5;
            }
        }
    }
}