.form {
    display: flex;
    flex-direction: column;
    justify-self: center;
    align-items: center;
    max-width: 450px;
    width: 100%;
    .form_title {
        text-align: center;
        font-size: 20px;
        line-height: 1.5;
        font-weight: bold;
    }
}
.input {
    margin-top: 30px;
    display: flex;
    max-width: 280px;
    width: 100%;
    height: 50px;
    background-color: #fff;
    box-shadow: 0 4px 15px rgb(0 0 0 / 20%);
    border: none;
    font-size: 18px;
    text-align: center;
    padding: 0 20px;
    outline: 0;
}

.button {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 280px;
    width: 100%;
    height: 50px;
    background-color: #33ccff;
    font-size: 18px;
    font-weight: bold;
    &:hover{
        text-decoration: underline;
    }
}
