.bringPhone {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 10%;
    right: 0;
    margin-right: 20px;
    .message {
        padding: 15px;
        display: flex;
        width: 200px;
        height: 85px;
        position: absolute;
        top: 0;
        margin-top: -100px;
        right: 0;
        background-color: rgba(112, 143, 198, 0.95);
        color: #fff;
        font-size: 13px;
        clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 85% 75%, 85% 100%, 70% 76%, 0% 75%);
    }
    .message_hiden {
        display: none;
    }
    .phone {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80px;
        height: 80px;
        background-color: rgba(112, 143, 198, 0.95);
        border-radius: 100%;
        cursor: pointer;
        .phone_content {
            .phone_content_svg {
                width: 40px;
                height: 40px;
                animation: 1s infinite alternate slidein;
            }
        }
    }
}

@keyframes slidein {
    from {
        transform: rotate(30deg);
    }
    to {
        transform: rotate(0deg);
    }
}
