#modal {
    position: relative;
    z-index: 10;
    .modal_container {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        left: 0;
        .modal_bg {
            width: 100%;
            height: 100%;
            background-color: #323232;
            opacity: 0.6;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 8;
        }
        .modal_content {
            padding: 40px;
            min-width: 250px;
            min-height: 280px;
            background-color: #fff;
            z-index: 9;
            position: relative;
            .modal_card_cross {
                position: absolute;
                top: 0;
                right: 0;
                margin-top: 10px;
                margin-right: 10px;
            }
        }
    }
}
