.headerContacts {
    display: flex;
    flex-direction: row;
    align-items: center;
    .headerContacts_img {
        display: flex;
        width: 40px;
        height: 40px;
    }
    .headerContacts_main {
        display: flex;
        flex-direction: column;
        align-items: center;
        .headerContacts_modal {
            font-weight: bold;
            color: #33ccff;
            cursor: pointer;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}
