.footer {
    display: flex;
    flex-direction: column;
    background-color: #000063;
    .footer_content {
        padding: 30px 0;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        color: #fff;
        .footer_social {
            display: flex;
            flex-direction: row;
            align-items: center;
            .footer_social_img {
                margin: 0 10px;
                width: 40px;
                height: 40px;
            }
        }
        .footer_contacts {
            display: flex;
            flex-direction: row;
            .footer_contacts_phones,
            .footer_contacts_outherInfo {
                display: flex;
                flex-direction: column;
            }
            .footer_contacts_phones {
                padding-top: 8px;
                padding-right: 30px;
                .phones_pad {
                    &:first-child {
                        padding-bottom: 10px;
                    }
                }
            }
            .footer_contacts_outherInfo {
                .mail {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    &:first-child {
                        padding-bottom: 10px;
                    }
                    .mail_img{
                        width: 30px;
                        height: 30px;
                    }
                }
            }
        }
    }
    .footer_bottom {
        padding: 30px 0;
        border-top: 1px solid #fff;
        .footer_bottom_content {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .footer_bottom_text {
                display: flex;
                font-size: 13px;
                color: #fff;
                .footer_bottom_link{
                    text-decoration: underline;
                }
            }
        }
    }
}
